import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { ApiService } from 'app/shared/services/api.service';
import * as moment from 'moment';

@Component({
  selector: 'app-report-details',
  templateUrl: './report-details.component.html',
  styleUrls: ['./report-details.component.scss']
})
export class ReportDetailsComponent implements OnInit {
  reportForm: FormGroup;
  reportName: any;
  reprts: any[] = [
    { reportName: "Reports",
    reportId:"ACRFXNB" ,date:"2019-11-29"
    },
    // { reportName: "Weekly Report" , reportId:"ITRWEEK",date:"2019-10-29"},
    // { reportName: "Attendance Report", reportId:"HRRATTD",date:"2022-08-23" },
    // { reportName: "Cash Flow Report" , reportId:"EPRCASH",date:"2020-02-01"},
    // { reportName: "Statement Of Foreign Assets And Liabilities", reportId:"FINRSOFAL" ,date:"2022-01-31"}
  ];
  report: any;
  reportupdate: any[];
  tellerId: string;
  constructor(private formBuilder: FormBuilder, private ls: LocalStoreService, private apiService: ApiService) { }

  ngOnInit(): void {
    this.reportName = this.reprts[0].reportName
    this.tellerId =localStorage.getItem('tellerid');
    this.buildReportForm();
    this.resetForm();
  }
  buildReportForm(data?) {
    this.reportForm = this.formBuilder.group({
      branch: [data ? data.branch :'', Validators.required],
      customerId: [data ? data.customerId : '', Validators.required],
      accountNo: [data ? data.accountNo : null],
      report: [data ? data.report : '', Validators.required],
      fromDate: [data ? data.fromDate : '', Validators.required],
      endDate: [data ? data.endDate : '',Validators.required],


    });
  }

  resetForm() {
    this.reportForm.reset();
  }
  gotoReport(reportName1: any) {
    console.log(reportName1);
    this.reportName = reportName1
    this.resetForm();
  }

  letterOnly(event) {
    var charCode = event.keyCode;
    if ((charCode > 64 && charCode < 91) || (charCode > 96 && charCode < 123) || charCode == 8)
      return true;
    else
      return false;
  }
  _keyPress(event: any) {
    // console.log("key",event);
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    inputChar = inputChar.toString();
    inputChar = inputChar
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }
  apply() {
    let payload = [{
      branch: this.reportForm.value.branch,
      customerId: this.reportForm.value.customerId,
      accountNo: this.reportForm.value.accountNo,
      report: this.reportForm.value.report ,
      fromDate: moment(this.reportForm.value.fromDate).format('YYYY-MM-DD'),
      endDate:  moment(this.reportForm.value.endDate).format('YYYY-MM-DD'),
      reportName: this.reportName
    }]
    this.reportupdate=this.reprts.filter(
      (val) => val.reportName===this.reportName);
      console.log(this.reportupdate);

   /* this.apiService.getReport().subscribe(data => {
      console.log(data)
      window.open(data);
    }) */
    this.apiService.getReport(this.reportForm.value.report,this.reportForm.value.customerId,moment(this.reportForm.value.fromDate).format('YYYY-MM-DD'),moment(this.reportForm.value.endDate).format('YYYY-MM-DD'),this.reportForm.value.branch);
  }
}
