import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';


import { SettingsComponent } from './settings/settings.component';
import { FlexLayoutModule } from '@angular/flex-layout';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { SharedMaterialModule } from 'app/shared/shared-material.module';
import { SharedModule } from 'app/shared/shared.module';
import { settingsRoutes } from './settings.routing';
import { ChangePasswordComponent } from './change-password/change-password.component';
import { ChangeEmailComponent } from './settings/change-email/change-email.component';
import { ThemeChangeComponent } from './theme-change/theme-change.component';
import { ReportDetailsComponent } from './report/report-details/report-details.component';




@NgModule({
  declarations: [SettingsComponent, ChangePasswordComponent, ChangeEmailComponent, ThemeChangeComponent,ReportDetailsComponent],
  imports: [
    CommonModule,
    FormsModule,
    FlexLayoutModule,
   
    SharedModule,
    ReactiveFormsModule,
    SharedMaterialModule,
    
    RouterModule.forChild(settingsRoutes)
  ]
})
export class SettingsModule { }
