
import {  Routes } from '@angular/router';
import { ChangePasswordComponent } from './change-password/change-password.component';
import { ReportDetailsComponent } from './report/report-details/report-details.component';
import { ChangeEmailComponent } from './settings/change-email/change-email.component';
import { SettingsComponent } from './settings/settings.component';
import { ThemeChangeComponent } from './theme-change/theme-change.component';
export const settingsRoutes: Routes = [
  {
    path: "",
    children: [
      {
        path: "profile",
        component: SettingsComponent,
        // data: { title: 'Blank', breadcrumb: 'BLANK' }
      },
      {
        path: "report/reportDetails",
        component: ReportDetailsComponent,
        // data: { title: 'Blank', breadcrumb: 'BLANK' }
      },
      {
        path:"change-password",
        component: ChangePasswordComponent
      },
      {
        path:"change-email",
        component:ChangeEmailComponent
      },
      {
        path:"theme-change",
        component:ThemeChangeComponent
      }
    ]
  }
];